<template>
  <div class="india">
    
  <div class="post-details">
        <div class="card mb-2" >
            <div class="card-body">
              <div class="row">
                <div class="col-12">
                  <div class="max-height"> 
                      <table class="table  table-dark table-striped">
                          <thead>
                              <tr class="table-dark text-dark sticky-top">
                                  <th>No.</th>
                                  <th class="text-center">
                                    <a href="#" @click="sortHighest" class="text-dark">
                                      Confirmed
                                    </a>
                                  </th>
                                  <th class="text-center">Deaths</th>
                                  <th class="text-center">Recovered</th>
                                  <th class="text-center">Active</th>
                                  <th class="text-center">
                                    <a href="#" @click="sortLowest" class="text-dark">
                                      Date
                                    </a>
                                  </th>
                              </tr>
                          </thead>
                          <tbody>
                              <tr v-for="(count, index) in indiaCount.slice().reverse()" :key="count.index">
                                <td>{{index + 1}}</td>
                                  <td class="text-center">
                                    <!-- <div v-if="index == indiaCount.length > 1000">yes</div> -->
                                    
                                    {{count.Confirmed}}
                                  </td>
                                  <td class="text-danger text-center">{{count.Deaths}}</td>
                                  <td class="text-success text-center">{{count.Recovered}}</td>
                                  <td class="text-warning text-center">{{count.Active}}</td>
                                  <td class="text-center">{{count.Date}}</td>
                              </tr>                        
                          </tbody>
                      </table>
                  </div>
                </div>
              </div>
            </div>
        </div>
    </div>


  </div>
</template>

<script>

import axios from "axios";

export default {
    name: "India",
    data() {
      return {
        indiaCount: [],
        errors: [],
        columns: ["Date", "Confirmed"],
        enableSort: false,
        sorting: -1
      }
    },
    created() {
      axios.get(`https://api.covid19api.com/total/country/india`)
      .then(res => {
        this.indiaCount = res.data
      })
      .catch(e => {
        this.errors.push(e);
      })
    },
    methods: {
     sortLowest() {
        this.indiaCount.sort((a, b) => a.Confirmed > b.Confirmed ? 1 : -1);
      },
      sortHighest() {
        this.indiaCount.sort((a, b) => a.Confirmed < b.Confirmed ? 1 : -1);
      }
    }
    
    // ---------------------------------
}
</script>

<style scoped>

.max-height {
    max-height: 500px;
    overflow: auto;
}

</style>