<template>
  <div class="about">
    <AddPost />
    <PostList v-bind:posts="posts" />
  </div>
</template>

<script>
import axios from "axios";

import AddPost from "@/views/AddPost.vue";
import PostList from "@/views/PostList.vue";

export default {
  components: {
    AddPost,
    PostList
  },
  data(){
    return {
      posts: [],      
      errors: []      
    }    
  },
  created() {
    axios.get(`https://jsonplaceholder.typicode.com/posts`)
    .then(res => {
      this.posts = res.data      
    })
    .catch(e => {
      this.errors.push(e);
    });
  }  
}
</script>